@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    box-sizing: border-box;
    padding: 0;
}

body {
    background-image: linear-gradient(120deg, rgb(137, 226, 226), rgb(57, 112, 214));
    color: rgb(1, 50, 114);
    font-family: "Rubik", sans-serif;
    min-height: 100vh;
}

header,
form {
    min-height: 20vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

form {
    min-height: 0vh;
}

form input {
    border-radius: 10px 0px 0px 10px;
}

form input,
form button {
    padding: 0.5rem;
    border: none;
    font-size: 20px;
}

form button {
    color: #d88771;
    background: #fff;
    cursor: pointer;
    border-radius: 0px 10px 10px 0px;
    transition: all 0.8s ease;
}

form button:hover {
    color: #fff;
    background: #d88771;
}

.todo-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.todo-list {
    min-width: 30%;
    list-style: none;
}

.todo {
    margin: 0.5rem;
    background: #fff;
    color: #000;
    font-size: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: all 0.8s ease;
}

.todo li {
    flex: 1 1;
}

.delete-btn,
.complete-btn {
    color: #ee5034;
    cursor: pointer;
    transition: all 0.8s ease;
    padding: 0.5rem;
    border: none;
    transition: all 0.5s ease;
}

.delete-btn {
    background-color: #eb5050;
    color: #000;
}

.delete-btn:hover {
    background-color: #000;
    color: #eb5050;
}
.complete-btn {
    color: #000;
    background-color: #58e458;
}

.complete-btn:hover {
    color: #58e458;
    background-color: #000;
}

.todo-item {
    padding: 0rem 0.5rem;
}

.fa-trash,
.fa-check {
    pointer-events: none;
}

.completed {
    text-decoration: line-through;
    opacity: 0.5;
}

.fall {
    transform: translateY(8rem) rotateZ(20deg);
    opacity: 0;
}

h4 {
    position: fixed;
    bottom: 0;
    opacity: 50%;
}

